.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.fill {
    height: 400;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

html {
  outline: none;
}

::-moz-selection {
  background: rgba(255,255,255,0.12); /* WebKit/Blink Browsers */
}

::selection {
  background: rgba(255,255,255,0.12); /* WebKit/Blink Browsers */
}

::-moz-selection {
  background: rgba(255,255,255,0.12); /* Gecko Browsers */
}



